import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex)


export const store = new Vuex.Store({
  state:{
    titleList:[], // 
    saveTitleList:[], // 
    category:[
      {
        title:'',
        path:'all',
        src:require('../../static/imgs/all.png'),
        typeNum:0
      },
      {
        title:'html',
        path:'h5',
        src:require('../../static/imgs/h5.png'),
      },
      {
        title:'css',
        path:'css',
        src:require('../../static/imgs/css.png')
      },
      {
        title:'js',
        path:'js',
        src:require('../../static/imgs/js.png')
      },
      {
        title:'php',
        path:'php',
        src:require('../../static/imgs/php.png')
      },
      {
        title:'mysql',
        path:'mysql',
        src:require('../../static/imgs/mysql.png')
      },
      {
        title:'server',
        path:'server',
        src:require('../../static/imgs/server.png')
      },
      {
        title:'others',
        path:'others',
        src:require('../../static/imgs/node.png')
      }
    ], // 
    detail:{
      id:'',
      createTime:'',
      title:'',
      user:'',
      visitor:'',
      content:''
    }, // 
    searchIsShow:true, // 
    searchResultIsShow:false, // 
    paginationIsShow:true,  // 
    pageNum:9, // 
    nowPageNum:1, //
  },
  getters:{
    nowPageNum:(state)=>{
      return state.nowPageNum
    },
    pageNum:(state)=>{
      return state.pageNum
    },
    paginationIsShow:(state)=>{
      return state.paginationIsShow
    },
    searchResultIsShow:(state)=>{
      return state.searchResultIsShow
    },
    searchIsShow:(state)=>{
      return state.searchIsShow
    },
    detail:(state)=>{
      return state.detail
    },
    category:(state)=>{
      return state.category
    },
    allData:(state)=>{
      return state.titleList;
    }
  },
  // mutations:{
  //   reducePrice:(state,payload)=>{
  //     state.products.forEach(product=>{
  //       product.price-=payload
  //     })
  //   }
  // },
  // actions:{
  //   reducePrice:(context,payload)=>{
  //     // setTimeout(function () {
  //       context.commit('reducePrice',payload)
  //     // },2000)
  //   }
  // }
});



